.header {
  position: fixed;
  top: 33px;
  width: 100%;
  z-index: 10;
}
.header__box {
  display: flex;
}
.header__logo {
  flex-basis: 30%;
}
.header__nav {
  flex-basis: 70%;
}
.header__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 10px;
  list-style: none;
}
.header__link {
  margin-top: -5px;
  display: block;
  position: relative;
  text-transform: uppercase;
  font-size: 12px;
}
.header__link:before {
  display: block;
  position: absolute;
  content: "";
  height: 2px;
  width: 0;
  left: 50%;
  bottom: -10px;
  background-color: #fff;
  transition: width 0.3s ease-in-out, left 0.3s ease-in-out;
}
.header__link:after {
  display: block;
  position: absolute;
  content: "";
  height: 2px;
  width: 0;
  left: 50%;
  bottom: -10px;
  background-color: #fff;
  transition: width 0.3s ease-in-out;
}
.header__link:hover:before {
  width: 50%;
  left: 0;
}
.header__link:hover:after {
  width: 50%;
}
.header__tel {
  display: block;
  margin-top: -8px;
  padding-left: 20px;
  border-left: 1px solid #fff;
  font-size: 16px;
  letter-spacing: 2.5px;
}
