  height: 39px;
  background: url(../img/svg/arrow_left.png) no-repeat center / cover;
}
.slick-next {
  right: 50px;
  width: 39px;
  height: 39px;
  background: url(../img/svg/arrow_right.png) no-repeat center / cover;
}
.slick-prev:before,
.slick-next:before {
  width: 0;
  height: 0;
}
.slick-prev:focus,
.slick-next:focus {
  opacity: 1;
}
.slick-disabled {
  opacity: .35!important;
}
.slick-dots {
  display: inline-block !important;
  left: -48px;
  top: 50%;
  transform: translateY(17%);
  border-image: transparent;
}
.slick-dots li {
  margin: 4px 8px;
  margin-bottom: 10px;
  width: 13px;
  height: 13px;
}
.slick-dots li button {
  background: #fff;
  display: block;
  height: 13px;
  width: 13px;
  font-size: 0px;
  color: #fff;
  border-radius: 50%;
  box-shadow: 0px 5px 7px 1px rgba(0, 45, 86, 0.15);
  opacity: 0.75;
}
.slick-dots li button:before {
  font-size: 0;
  opacity: 0;
  display: none;
}
.slick-dots li.slick-active button {
  background: #fb0035;
  border-radius: 50%;
  opacity: 1;
}
@media only screen and (min-width: 768px) {
  .slick-dots li.slick-active button {
    background: #fff;
  }
}
.slick-current,
.slick-slide,
.slick-active {
  outline: none!important;
}
